console.log("JS Connected!");

async function addToCart(productId, quantity) {
    try {
        // Construct the request body
        const requestBody = {
            productId: productId,
            quantity: quantity
        };

        // Make a fetch request to add the item to the cart
        const response = await fetch('/api/cart/add', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(requestBody)
        });

        if (!response.ok) {
            throw new Error('Failed to add item to cart');
        }

        console.log('Item added to cart');
        // Optionally, you can update the UI here to reflect the item added to the cart

    } catch (error) {
        console.error('Error:', error);
        // Handle error here, maybe show a message to the user
    }
}

function setupButtons() {
    document.querySelectorAll('.add-to-cart-btn').forEach(button => {
        button.addEventListener('click', () => {
            const productId = button.getAttribute('data-product-id');
            // Assuming you want to add 1 quantity by default
            const quantity = 1;
            addToCart(productId, quantity);
        });
    });
}

async function fetchCartItems() {
    try {
        const response = await fetch('/api/cart');

        if (!response.ok) {
            throw new Error('Error fetching cart items');
        }

        const cartItems = await response.json();
        console.log('Cart Items:', cartItems);
        // Update UI with cart items

    } catch (error) {
        console.error('Error:', error);
        // Handle error here, maybe show a message to the user
    }
}

function toggleCart() {
    document.body.classList.toggle('showCart');
}

document.getElementById('cartIcon').addEventListener('click', toggleCart);

document.getElementById('loginForm').addEventListener('submit', async function(event) {
    event.preventDefault();
    const username = document.getElementById('username').value;
    const password = document.getElementById('password').value;

    try {
        const response = await fetch('/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ username, password })
        });

        if (response.ok) {
            closeModal();
            console.log('Login Success');
            // Redirect or update UI after successful login
        } else {
            const errorData = await response.json();
            console.error('Login Error:', errorData.error);
            // Display error message on the page
        }
    } catch (error) {
        console.error('Login Error:', error);
        // Display error message on the page
    }
});

function closeModal() {
    document.getElementById('loginModal').style.display = 'none';
}

// Ensure DOM is fully loaded before setting up buttons
document.addEventListener('DOMContentLoaded', () => {
    setupButtons();
    // You might want to call fetchCartItems here if you want to load cart items on page load
    fetchCartItems();
});
